@font-face {
  font-weight: normal;
  font-family: "Geometria";
  font-style: normal;
  font-display: auto;
  src: url("../fonts/Geometria-Regular.woff2") format("woff2"), url("../fonts/Geometria-Regular.woff") format("woff");
}

@font-face {
  font-weight: 500;
  font-family: "Geometria";
  font-style: normal;
  font-display: auto;
  src: url("../fonts/Geometria-Medium.woff2") format("woff2"), url("../fonts/Geometria-Medium.woff") format("woff");
}

@font-face {
  font-weight: 300;
  font-family: "Geometria";
  font-style: normal;
  font-display: auto;
  src: url("../fonts/Geometria-Light.woff2") format("woff2"), url("../fonts/Geometria-Light.woff") format("woff");
}

html,
body {
  min-width: 375px;
}

body {
  padding: 0;
  margin: 0 auto;
  width: auto;
  font-family: "Geometria", "Arial", sans-serif;
  font-size: 12px;
  line-height: 16px;
  background-color: #fbf5f4;
  color: black;
}

@media (min-width: $tablet-width) {
  body {
    width: auto;
  }
}

@media (min-width: $desktop-width) {
  body {
    width: auto;
  }
}
