.main-nav {
  display: block;
  position: fixed;
  list-style: none;
  padding-top: 25px;
  margin: 0;
  box-sizing: border-box;
  box-shadow: 0 3px 4px -4px #444;
  width: 270px;
  height: 100%;
  background-color: #fbf5f4;
  left: -280px;
  transition: all .5s;
  z-index: 2;
  transform: translateZ(0);
  backface-visibility: hidden;
}

.main-nav__list {
  margin: 0;
  margin-top: 10px;
  padding: 0;
  list-style: none;
}

.main-nav__item {
  padding-left: 40px;
  margin-bottom: 10px;
  font-size: 14px;
}

.main-nav__item a {
  display: inline-block;
  text-decoration: none;
  color: #fc66a2;
  line-height: 1;
  cursor: pointer;
}

.main-nav__item--active a {
  opacity: 0.5;
}

.main-nav__item a::after {
  display: block;
  content: "";
  height: 2px;
  width: 0;
  background-color: #f78e6a;
  transition: width .3s ease-in-out;
}

.main-nav__item a:hover::after,
.main-nav__item a:focus::after {
  width: 100%;
}

.main-nav__wrapper {
  margin: 0;
  padding: 0;
  list-style: none;
  padding-left: 40px;
  padding-bottom: 10px;
  border-bottom: 1px solid #4444;
}

.main-nav__wrapper-item a {
  text-decoration: none;
  color: #707070;
  font-size: 13px;
}

.main-nav__wrapper-item a:hover {
  color: #f78e6a;
}

.main-nav__wrapper-item img {
  margin-right: 5px;
}
