.page-header {
  width: 100%;
}

.page-header__container {
  position: relative;
  display: flex;
  justify-content: space-between;
  margin: 0 auto;
}

.page-header__logo-image {
  margin-top: 14px;
}

.page-header__logo {
  padding-top: 14px;
  padding-right: 16px;
}

.page-header__button {
  display: none;
}

.page-header__btn-menu {
  position: absolute;
  top: 16px;
  left: 30px;
  cursor: pointer;
  transition: right .23s;
  z-index: 3;
  width: 20px;
  transform: translateZ(0);
  backface-visibility: hidden;
}

.page-header__text {
  display: none;
}

.page-header__btn-menu span {
  display: block;
  height: 2px;
  background-color: #f78e6a;
  margin: 5px 0 0;
  transition: all .1s linear .23s;
  position: relative;
}

.page-header__third {
  width: 14px;
}

.page-header__btn-menu span::before {
  content: "";
  position: absolute;
  left: -4px;
  width: 2px;
  height: 2px;
  background-color: black;
}

.page-header__button:checked ~ .page-header__btn-menu {
  left: 230px;
  top: 25px;
  width: 16px;
}

.page-header__button:checked ~ .main-nav {
  left: 0;
}

.page-header__button:checked ~ .page-header__btn-menu span::before {
  display: none;
}

.page-header__button:checked ~ .page-header__btn-menu span.page-header__first {
  width: 20px;
  transform: rotate(45deg);
}

.page-header__button:checked ~ .page-header__btn-menu span.page-header__second {
  display: none;
}

.page-header__button:checked ~ .page-header__btn-menu span.page-header__third {
  transform: rotate(-45deg);
  top: -7px;
  width: 20px;
}

.page-header__title,
.page-header__phone {
  display: none;
}

@media (min-width: $desktop-width) {
  .page-header__btn-menu {
    top: 40px;
    left: 90px;
  }

  .page-header__text {
    position: absolute;
    top: 6px;
    left: 35px;
    display: block;
    font-size: 18px;
    font-weight: normal;
  }

  .page-header__button:checked ~ .page-header__btn-menu div.page-header__text {
    display: none;
  }

  .page-header__logo {
    position: absolute;
    top: 511px;
    left: 90px;
  }

  .page-header__logo img {
    width: 49px;
    height: 30px;
  }

  .page-header__title,
  .page-header__phone {
    display: block;
    margin-top: 40px;
    font-size: 18px;
    font-weight: normal;
    line-height: 2;
  }

  .page-header__title {
    position: relative;
  }

  .page-header__title::before {
    content: "";
    position: absolute;
    top: 13px;
    left: -15px;
    width: 6px;
    height: 6px;
    border-radius: 3px;
    background-color: #f78e6a;
  }

  .page-header__phone {
    margin-right: 90px;
  }
}
