.form {
  margin-top: 20px;
}

.form__block {
  margin-bottom: 40px;
  padding-bottom: 20px;
  padding-top: 10px;
  width: 335px;
  font-size: 16px;
  font-weight: 300;
  background: none;
  border: none;
  border-bottom: 1px solid #4444;
}

.form__wrapper {
  position: relative;
}

.form__wrapper img {
  position: absolute;
  top: 18px;
  left: 17px;
  z-index: 1;
}

.form__button {
  position: relative;
  padding: 21px 51px 21px 80px;
  text-decoration: none;
  font-size: 16px;
  font-weight: normal;
  color: white;
  border: none;
  border-radius: 29px;
  box-shadow: 0 0 50px 0 rgba(0, 0, 0, 0.1);
  background-color: #f78e6a;
}

.form__button::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 60px;
  height: 60px;
  border-radius: 30px;
  background-image: linear-gradient(to right, #f78e6a, #fc66a2);
}

@media (min-width: $desktop-width) {
  .form {
    margin-top: 40px;
    width: 430px;
  }

  .form__block {
    margin-bottom: 30px;
    width: 430px;
  }

  .form__wrapper {
    margin-top: 30px;
  }
}
