.timetable {
  width: 375px;
  margin: 0 auto;
}

.timetable__container {
  overflow-x: scroll;
  overflow-y: visible;
}

.timetable__table {
  margin-top: 40px;
  margin-left: 20px;
  margin-bottom: 30px;
  width: 100%;
  table-layout: fixed;
}

.timetable__item,
.timetable__class,
.timetable__time {
  position: relative;
  width: 201px;
  height: 76px;
  background-color: white;
  border: 0.01px solid #fbf5f4;
}

.timetable__class {
  font-size: 14px;
  font-weight: 300;
  text-align: center;
}

.timetable__time {
  font-size: 14px;
  font-weight: bold;
  text-align: center;
  color: #f78e6a;
}

.timetable__item {
  position: relative;
  text-align: left;
  font-size: 14px;
  font-weight: 300;
  color: #010101;
}

.timetable__item::before {
  content: "";
  position: absolute;
  top: 23px;
  left: 31px;
  width: 30px;
  height: 30px;
  border-radius: 15px;
  background-image: linear-gradient(to top, #f78e6a, #fc66a2);
}

.timetable__title {
  width: 100px;
  padding-left: 81px;
}

.timetable__title::before {
  content: "";
  position: absolute;
  top: 34px;
  left: 44px;
  width: 4px;
  height: 6px;
  background-color: white;
}

.timetable__title::after {
  content: "";
  position: absolute;
  top: 40px;
  left: 42px;
  width: 0;
  height: 0;
  border: 5px solid;
  border-color: white;
  border-bottom: 0;
  border-right: 4px solid transparent;
  border-left: 4px solid transparent;
}
