.contacts {
  width: 375px;
  margin: 0 auto;
}

.contacts__container {
  margin-left: 20px;
}

.contacts__image {
  position: relative;
  height: 280px;
}

.contacts__image img {
  position: absolute;
  top: -42px;
  left: -20px;
  z-index: -1;
}

.contacts__list,
.contacts__link {
  margin: 0;
  margin-bottom: 30px;
  padding: 0;
  list-style: none;
}

.contacts__title {
  font-size: 14px;
  font-weight: 300;
  line-height: 1.67;
}

.contacts__text {
  font-size: 18px;
  font-weight: 300;
  line-height: 1.67;

  &--bottom {
    margin-bottom: 15px;
  }
}

.contacts__link-item {
  display: flex;

  &--bottom {
    margin-bottom: 15px;
  }
}

.contacts__link-image {
  margin-right: 10px;
  width: 30px;
  height: 30px;
  background-image: linear-gradient(to top, #f78e6a, #fc66a2);
  border-radius: 15px;
}

.contacts__link-image--top img {
  margin-top: 5px;
}

.contacts__link-item a {
  font-size: 14px;
  font-weight: 300;
  line-height: 1.71;
  color: #000000;
}

.contacts__company {
  font-size: 14px;
  font-weight: 300;
  line-height: 2.14;
}

.contacts__button {
  position: relative;
  margin-top: 60px;
  margin-bottom: 65px;
}

.contacts__button a {
  padding: 20px 51px 20px 80px;
  text-decoration: none;
  font-size: 16px;
  font-weight: normal;
  color: white;
  border-radius: 29px;
  box-shadow: 0 0 50px 0 rgba(0, 0, 0, 0.1);
  background-color: #f78e6a;
}

.contacts__button::before {
  content: "";
  position: absolute;
  top: -22px;
  width: 60px;
  height: 60px;
  border-radius: 30px;
  background-image: linear-gradient(to right, #f78e6a, #fc66a2);
}

.contacts__button a::after {
  content: "";
  position: absolute;
  left: 28px;
  width: 2px;
  height: 16px;
  background-color: white;
}

.contacts__button a::before {
  content: "";
  position: absolute;
  left: 28px;
  width: 2px;
  height: 16px;
  transform: rotate(90deg);
  background-color: white;
}
