.discount {
  width: 375px;
  margin: 0 auto;
}

.discount__container {
  margin-left: 21px;
}

.discount__header {
  margin: 0;
  margin-top: 70px;
  font-size: 48px;
  font-weight: 300;
  line-height: 0.73;
  color: #000000;
}

.discount__header span {
  font-size: 80px;
  font-weight: 300;
  line-height: 1.1;
  color: #f78e6a;
  background-clip: text;
  background-image: linear-gradient(to bottom, #fc66a2, #f78e6a), linear-gradient(to bottom, #f78e6a, #f78e6a);
  -webkit-text-fill-color: transparent;
}

.discount__description {
  margin: 0;
  margin-top: 35px;
  width: 260px;
  font-size: 16px;
  font-weight: 300;
  line-height: 1.88;

  &--width {
    width: 310px;
  }
}

.discount__information {
  margin: 0;
  margin-top: 30px;
  margin-bottom: 71px;
  width: 295px;
  font-size: 12px;
  font-weight: 200;
  line-height: 1.67;
}

.discount__information a {
  color: black;
}

.discount__image {
  display: none;
}

@media (min-width: $desktop-width) {
  .discount {
    width: 100%;
  }

  .discount__container {
    display: flex;
    margin-left: 256px;
    margin-top: 152px;

    &--top {
      margin-top: 100px;
    }
  }

  .discount__image {
    display: block;
    margin-right: 160px;
  }

  .discount__header {
    margin-top: 0;
    width: 330px;
  }

  .discount__description {
    width: 437px;
    font-size: 18px;
  }

  .discount__information {
    margin-bottom: 179px;
  }

  .discount__wrapper--top {
    margin-top: 71px;
  }
}
