.classes {
  width: 375px;
  margin: 0 auto;
}

.classes__container {
  margin-left: 21px;
}

.classes__header {
  position: relative;
  margin-top: 80px;
}

.classes__title {
  margin: 0;
  height: 44px;
  font-weight: normal;
  font-size: 48px;
  line-height: 0.75;
  color: #f78e6a;
  background-clip: text;
  background-image: linear-gradient(to bottom, #f78e6a, #fc66a2), linear-gradient(to bottom, #f78e6a, #f78e6a);
  -webkit-text-fill-color: transparent;

  &--size {
    display: none;
  }
}

.classes__text {
  margin: 0;
  margin-top: 20px;
  margin-bottom: 40px;
  font-size: 24px;
  font-weight: normal;
  line-height: 1.5;
  color: black;
}

.classes__arrow-back {
  position: absolute;
  top: 7px;
  left: 264px;
  width: 30px;
  height: 30px;
  border-radius: 20px;
  background-image: linear-gradient(to top, #f78e6a, #fc66a2);
}

.classes__arrow-back::before {
  content: "";
  position: absolute;
  top: 12px;
  left: 11px;
  width: 0;
  height: 0;
  border: 6px solid;
  border-color: white;
  border-left: 0;
  border-top: 3px solid transparent;
  border-bottom: 3px solid transparent;
}

.classes__arrow-next {
  position: absolute;
  top: 7px;
  left: 304px;
  width: 30px;
  height: 30px;
  border-radius: 20px;
  background-image: linear-gradient(to top, #f78e6a, #fc66a2);
}

.classes__arrow-next::before {
  content: "";
  position: absolute;
  top: 12px;
  left: 13px;
  width: 0;
  height: 0;
  border: 6px solid;
  border-color: white;
  border-right: 0;
  border-top: 3px solid transparent;
  border-bottom: 3px solid transparent;
}

.classes__block {
  margin: 0;
  font-size: 16px;
  font-weight: 300;
  line-height: 1.88;
  color: black;

  &--second {
    margin-top: 37px;
    margin-bottom: 50px;
    width: 315px;
  }
}

.classes__button {
  position: relative;
  margin-bottom: 110px;
}

.classes__button a {
  padding: 20px 51px 20px 80px;
  text-decoration: none;
  font-size: 16px;
  font-weight: normal;
  color: white;
  border-radius: 29px;
  box-shadow: 0 0 50px 0 rgba(0, 0, 0, 0.1);
  background-color: #f78e6a;
}

.classes__button::before {
  content: "";
  position: absolute;
  top: -22px;
  width: 60px;
  height: 60px;
  border-radius: 30px;
  background-image: linear-gradient(to right, #f78e6a, #fc66a2);
}

.classes__button a::after {
  content: "";
  position: absolute;
  left: 28px;
  width: 2px;
  height: 16px;
  background-color: white;
}

.classes__button a::before {
  content: "";
  position: absolute;
  left: 28px;
  width: 2px;
  height: 16px;
  transform: rotate(90deg);
  background-color: white;
}

.classes__list,
.classes__image {
  display: none;
}

@media (min-width: $desktop-width) {
  .classes {
    width: 100%;
  }

  .classes__list,
  .classes__image,
  .classes__title--size {
    display: block;
  }

  .classes__arrow-back,
  .classes__arrow-next {
    display: none;
  }

  .classes__header {
    margin-top: 140px;
  }

  .classes__container {
    display: flex;
    margin-left: 177px;
  }

  .classes__title {
    font-size: 48px;

    &--size {
      margin-top: 140px;
      width: 80px;
      font-size: 18px;
      font-weight: 500;
      line-height: 1.67;
    }
  }

  .classes__list {
    margin: 0;
    margin-top: 135px;
    margin-right: 75px;
    padding: 0;
  }

  .classes__item {
    width: 160px;
    list-style: none;
    font-size: 18px;
    font-weight: normal;
    line-height: 2.3;
    color: rgba(0, 0, 0, 0.5);
    
    &--active {
      color: #000000;
    }
  }

  .classes__image {
    margin-right: 105px;
  }

  .classes__block {
    width: 492px;
    font-size: 18px;
    line-height: 1.94;

    &--second {
      margin-top: 20px;
    }
  }
}
