.teacher {
  width: 375px;
  margin: 0 auto;
}

.teacher__container {
  margin-left: 20px;
}

.teacher__wrapper {
  position: relative;
  height: 300px;
}

.teacher__image img {
  position: absolute;
  top: -26px;
  left: 94px;
}

.teacher__image::before {
  content: "";
  position: absolute;
  top: 25px;
  left: 117px;
  width: 124px;
  height: 125px;
  border-radius: 65px;
  background-image: linear-gradient(to top, #f78e6a, #fc66a2);
}

.teacher__arrow-back {
  position: absolute;
  top: 110px;
  left: 0;
  width: 30px;
  height: 30px;
  border-radius: 20px;
  background-image: linear-gradient(to top, #f78e6a, #fc66a2);
}

.teacher__arrow-back::before {
  content: "";
  position: absolute;
  top: 12px;
  left: 11px;
  width: 0;
  height: 0;
  border: 6px solid;
  border-color: white;
  border-left: 0;
  border-top: 3px solid transparent;
  border-bottom: 3px solid transparent;
}

.teacher__arrow-next {
  position: absolute;
  top: 110px;
  left: 305px;
  width: 30px;
  height: 30px;
  border-radius: 20px;
  background-image: linear-gradient(to top, #f78e6a, #fc66a2);
}

.teacher__arrow-next::before {
  content: "";
  position: absolute;
  top: 12px;
  left: 13px;
  width: 0;
  height: 0;
  border: 6px solid;
  border-color: white;
  border-right: 0;
  border-top: 3px solid transparent;
  border-bottom: 3px solid transparent;
}

.teacher__title {
  margin: 0;
  font-size: 40px;
  font-weight: normal;
  line-height: 0.9;
}

.teacher__text {
  margin: 0;
  margin-top: 5px;
  font-size: 18px;
  font-weight: normal;
  line-height: 2;
}

.teacher__description {
  margin: 0;
  margin-top: 20px;
  width: 332px;
  font-size: 16px;
  font-weight: 300;
  line-height: 1.88;
}

.teacher__button {
  position: relative;
  margin-top: 50px;
  margin-bottom: 95px;
}

.teacher__button a {
  padding: 20px 51px 20px 80px;
  text-decoration: none;
  font-size: 16px;
  font-weight: normal;
  color: white;
  border-radius: 29px;
  box-shadow: 0 0 50px 0 rgba(0, 0, 0, 0.1);
  background-color: #f78e6a;
}

.teacher__button::before {
  content: "";
  position: absolute;
  top: -22px;
  width: 60px;
  height: 60px;
  border-radius: 30px;
  background-image: linear-gradient(to right, #f78e6a, #fc66a2);
}

.teacher__button a::after {
  content: "";
  position: absolute;
  left: 28px;
  width: 2px;
  height: 16px;
  background-color: white;
}

.teacher__button a::before {
  content: "";
  position: absolute;
  left: 28px;
  width: 2px;
  height: 16px;
  transform: rotate(90deg);
  background-color: white;
}

.teacher__title-list,
.teacher__list,
.teacher__description--desktop {
  display: none;
}

@media (min-width: $desktop-width) {
  .teacher {
    width: 100%;
  }

  .teacher__container {
    display: flex;
    margin-top: 55px;
    margin-left: 160px;
  }

  .teacher__title-list,
  .teacher__list,
  .teacher__description--desktop {
    display: block;
  }

  .teacher__arrow-next,
  .teacher__arrow-back {
    display: none;
  }

  .teacher__image img {
    position: static;
    z-index: 1;
    margin-right: 126px;
  }

  .teacher__title-list {
    margin-top: 89px;
    font-size: 18px;
    font-weight: 500;
    line-height: 1.67;
    color: #f78e6a;
    background-clip: text;
    background-image: linear-gradient(to top, #f78e6a, #fc66a2), linear-gradient(to bottom, #f78e6a, #f78e6a);
    -webkit-text-fill-color: transparent;
  }

  .teacher__image::before {
    z-index: -1;
    top: 134px;
    left: 64px;
    width: 335px;
    height: 335px;
    border-radius: 170px;
  }

  .teacher__list {
    margin: 0;
    margin-top: 84px;
    margin-right: 57px;
    margin-left: 10px;
    padding: 0;
  }

  .teacher__item {
    width: 160px;
    list-style: none;
    font-size: 18px;
    font-weight: normal;
    line-height: 2.3;
    color: rgba(0, 0, 0, 0.5);

    &--active {
      color: #000000;
    }
  }

  .teacher__block {
    margin-top: 89px;
  }

  .teacher__title {
    font-size: 48px;
    line-height: 0.75;
    color: #000000;
  }

  .teacher__text {
    margin-top: 15px;
    font-size: 24px;
    line-height: 1.5;
  }

  .teacher__description {
    margin-top: 40px;
    width: 460px;
    font-size: 18px;
    line-height: 1.67;

    &--desktop {
      margin-top: 25px;
    }
  }

  .teacher__button {
    margin-top: 80px;
    margin-bottom: 260px;
  }
}
