.tickets {
  width: 375px;
  margin: 0 auto;
}

.tickets__container {
  overflow-x: scroll;
  overflow-y: visible;
}

.tickets__table {
  margin-top: 75px;
  margin-left: 20px;
  width: 100%;
  table-layout: fixed;
}

.tickets__item {
  position: relative;
  width: 280px;
  height: 140px;
  background-color: white;
  border: 2px solid #fbf5f4;
}

.tickets__item::before {
  content: "";
  position: absolute;
  top: 50px;
  left: 220px;
  width: 40px;
  height: 40px;
  border-radius: 20px;
  background-color: #f7f3f2;
}

.tickets__item--active::before {
  background-image: linear-gradient(to right, #f78e6a, #fc66a2);
  background-color: #f78e6a;
}

.tickets__title {
  position: relative;
  margin-left: 40px;
  font-size: 18px;
  font-weight: 300;
  line-height: 1.11;
}

.tickets__title::after {
  content: "";
  position: absolute;
  top: 22px;
  left: 198px;
  width: 2px;
  height: 12px;
  background-color: #4444;
}

.tickets__title::before {
  content: "";
  position: absolute;
  top: 22px;
  left: 198px;
  width: 2px;
  height: 12px;
  transform: rotate(90deg);
  background-color: #4444;
}

.tickets__title--active::before {
  background-color: white;
}

.tickets__title--active::after {
  background-color: white;
}

.tickets__price span {
  margin-left: 41px;
  font-size: 24px;
  font-weight: bold;
  line-height: 0.83;
  color: #f78e6a;
  background-clip: text;
  background-image: linear-gradient(to top, #f78e6a, #fc66a2), linear-gradient(to bottom, #f78e6a, #f78e6a);
  -webkit-text-fill-color: transparent;
}

.tickets__price {
  margin-top: 20px;
  font-size: 18px;
  font-weight: 300;
}

.tickets__button {
  position: relative;
  margin-top: 55px;
  margin-bottom: 90px;
  margin-left: 20px;
}

.tickets__button a {
  padding: 20px 51px 20px 80px;
  text-decoration: none;
  font-size: 16px;
  font-weight: normal;
  color: white;
  border-radius: 29px;
  box-shadow: 0 0 50px 0 rgba(0, 0, 0, 0.1);
  background-color: #f78e6a;
}

.tickets__button::before {
  content: "";
  position: absolute;
  top: -22px;
  width: 60px;
  height: 60px;
  border-radius: 30px;
  background-image: linear-gradient(to right, #f78e6a, #fc66a2);
}

.tickets__button a::after {
  content: "";
  position: absolute;
  top: 6px;
  left: 26px;
  width: 2px;
  height: 5px;
  transform: rotate(-45deg);
  background-color: white;
}

.tickets__button a::before {
  content: "";
  position: absolute;
  top: 1px;
  left: 32px;
  width: 2px;
  height: 12px;
  transform: rotate(45deg);
  background-color: white;
}
